//https://developer.spotify.com/doucumentation/web-playback-sdk/quick-start/#
 
export const autoEndpoint =
  "https://accounts.spotify.com/authorize";
const redirectUri = "http://localhost:3000/";
const clientId = "7101b92d35364b53a65e5f9894ab177a";

const scopes = [
  "user-read-currently-playing",
  "user-read-recently-played",
  "user-read-playback-state",
  "user-top-read",
  "user-modify-playback-state",
];

export const getTokenFormUrl = () => {
  
  return window.location.hash
    .substring( 1 )
    .split( "&" )
    .reduce( ( initial, item ) => {
      var parts = item.split( "=" );
      initial[ parts[ 0 ] ] = decodeURIComponent( parts[ 1 ] );

      return initial;
    }, {} );
}


export const loginUrl = `${autoEndpoint}?client_id=${clientId}&redirect_uri=${redirectUri}&scope=${scopes.join( "%20" )}&response_type=token&show_dialog=true`;